// Commons
import { Helper } from 'Commons';

// Components
import CustomSelect from 'Components/b8one.customSelect.js';
import AccordionComponent from 'Components/b8one.accordionComponent.js';

// Repositories
import MasterdataRepository from 'Repositories/b8one.masterdataRepository.js';

// Services
import CartService from 'Services/b8one.cartService.js';
import TransporterService from 'Services/b8one.transporterService.js';

/*
  Class TransporterPage
*/
class TransporterPage {
  constructor() {
    this.modalCompare = null;
    this.addressCep = null;
    this.transporterSelected = null;
    this.transporters = [];
    this.logisticsInfo = [];
    this.filters = new Set();
    this.compareTransportersList = new Set();
    this.masterdataRepository = new MasterdataRepository('TR');
    this.cartService = new CartService();
    this.helper = new Helper();
    this.orderBy = 'less-price';
    this.totalizers = [];
    this.orderForm = {};
    this.hubID = null;
    this.transporterService = new TransporterService();

    window.TransporterPage = this;

    this.init();
  }

  // Init Functions
  async init() {
    this.initFilterAccordions();
    this.createCustomSelect();

    this.orderForm = await this.cartService.getCurrent();

    await this.setAddressCep();

    this.setHubID();

    this.transporters = await this.fetchTransporters();

    await Promise.all([
      this.getDeliveryTypes(),
      this.setLogoUrlInTransportes(),
    ]);

    this.orderTransportersByLessPrice();

    this.addTransportersToDOM();

    this.setHandledFiltersChange();
    this.setHandledsToTransporterElement();
    this.setHandledScheduleChangeButtonsClick();
    this.setHandledButtonSaveClick();
  }

  initFilterAccordions() {
    const accordions = [...document.querySelectorAll('.js-accordion-filter')];

    accordions.forEach(accordion => {
      const accordionHeader = accordion.querySelector('.filter__header');

      const accordionInstance = new AccordionComponent({
        accordion,
        accordionHeader,
        openAccordionCallback: () => {
          accordion.classList.add('filter--show');
        },
        closeAccordionCallback: ({ accordion }) => {
          accordion.classList.remove('filter--show');
        },
      });

      accordionInstance.isOpen = true;
    });
  }

  // Set Handled Functions
  setHandledFiltersChange() {
    const filterItems = [...document.querySelectorAll('.filter__item')];

    filterItems.forEach(filterItem =>
      filterItem.addEventListener('change', event =>
        this.handledFilterChange(event, filterItem)
      )
    );
  }

  setHandledsToTransporterElement() {
    const transporters = [
      ...document.querySelectorAll('.compare-transporter__list .transporter'),
    ];

    transporters.forEach(transporter => {
      const priceElements = [...transporter.querySelectorAll('.price')];

      priceElements.forEach(priceElement => {
        priceElement.addEventListener('change', event => {
          const { transporterDeliveryType } = priceElement.dataset;

          transporter.setAttribute(
            'data-transporter-delivery-type-selected',
            transporterDeliveryType
          );
        });
      });
    });
  }

  setHandledScheduleChangeButtonsClick() {
    const scheduleChangeButtons = [
      ...document.querySelectorAll('.js-button-schedule-change'),
    ];

    scheduleChangeButtons.forEach(scheduleChangeButton => {
      scheduleChangeButton.addEventListener('click', event =>
        this.handledScheduleChangeButtonClick(scheduleChangeButton)
      );
    });
  }

  setHandledButtonSaveClick() {
    const buttonSave = document.querySelector('.js-button-save');

    buttonSave.addEventListener('click', () => this.handledButtonSaveClick());
  }

  // Handled Functions
  handledFilterChange({ target }, filterItem) {
    const { filter: filterName } = filterItem.dataset;

    if (target.checked) {
      this.filters.add(filterName);
    } else {
      this.filters.delete(filterName);
    }

    this.transporters.forEach(transporter => {
      const transporterElement = document.querySelector(
        `[id="${transporter.id}"]`
      );

      const filtersArray = [...this.filters].map(
        filter => !!transporter[filter]
      );

      if (transporterElement) {
        filtersArray.includes(false)
          ? transporterElement.classList.add('hide')
          : transporterElement.classList.remove('hide');
      }
    });
  }

  handledTransporterCompareButtonClick(compareButton, transporterID) {
    compareButton.classList.toggle('transporter__compare-button--active');

    const toCompare = compareButton.classList.contains(
      'transporter__compare-button--active'
    );

    if (toCompare) {
      this.compareTransportersList.add(transporterID);
    } else {
      this.compareTransportersList.delete(transporterID);
    }

    this.updateTransporterFooter();
  }

  handledCompareButtonFooterClick() {
    const compareButton = document.querySelector(
      '.compare-transporter__footer .button'
    );

    compareButton.addEventListener('click', event => {
      const columns = [...this.compareTransportersList]
        .map(transporterID => {
          return this.createCompareTableColumn(transporterID);
        })
        .join('');

      const compareTable = document.querySelector(
        '.modal-compare .compare__table'
      );

      compareTable.innerHTML = compareTable.children[0].outerHTML;

      compareTable.innerHTML += columns;

      this.setHandledScheduleChangeButtonsClick();
      this.createTransportersCustomSelects();

      this.modalCompare.show();
    });
  }

  async handledScheduleChangeButtonClick(scheduleChangeButton) {
    const transporterElement = Helper.getParents(
      scheduleChangeButton,
      '.transporter'
    )[0];

    this.setTransporterSelected(transporterElement);

    await this.saveData();

    if (this.transporterSelected) {
      window.location.assign('/endereco');
    }
  }

  async handledButtonSaveClick() {
    const transporterElement = Helper.getParents(
      priceActive,
      '.transporter'
    )[0];

    this.setTransporterSelected(transporterElement);

    document.dispatchEvent(window.initSaveCustomDataEvent);

    await this.saveData();

    document.dispatchEvent(window.endSaveCustomDataEvent);
  }

  // Create Functions
  createTransportersCustomSelects() {
    const transportersSelects = [
      ...document.querySelectorAll('.transporter__select-custom'),
    ];

    transportersSelects.forEach(transporterSelect => {
      const selectElement = transporterSelect.querySelector('.select__native');
      const selectCustom = transporterSelect.querySelector('.select__custom');
      const customSelectList = transporterSelect.querySelector('.select__list');
      const customSelectSelected = transporterSelect.querySelector(
        '.select__custom-selected'
      );

      const transporterElement = Helper.getParents(
        transporterSelect,
        '.transporter'
      )[0];

      new CustomSelect({
        selectElement,
        customSelectList,
        customSelectSelected,
        customSelectListItemClassName: '',
        customSelectListItemClassNameActive: 'active',
        selectOpenCallback: event => {
          const selectListHeight = customSelectList.offsetHeight;

          selectCustom.classList.add('select__custom--open');
          selectCustom.style.maxHeight = `${selectListHeight}px`;
        },
        selectCloseCallback: () => {
          selectCustom.classList.remove('select__custom--open');
          selectCustom.style.maxHeight = 0;
        },
        selectChangeCallback: ({ value }) => {
          transporterElement.setAttribute(
            'data-transporter-delivery-type-selected',
            value
          );
        },
      });
    });
  }

  createCustomSelect() {
    const filterSelect = document.querySelector(
      '.header__actions .filter__select'
    );

    const customSelect = new CustomSelect({
      selectElement: '.header__actions .filter__select .select__native',
      customSelectList: '.header__actions .select__list',
      customSelectSelected: '.header__actions .select__custom-selected',
      customSelectListItemClassName: '',
      customSelectListItemClassNameActive: 'active',
      selectOpenCallback: event => {
        filterSelect.classList.add('filter__select--open');
      },
      selectCloseCallback: () => {
        filterSelect.classList.remove('filter__select--open');
      },
      selectChangeCallback: ({ value }) => {
        this.orderTransportersBy(value);
      },
    });
  }

  orderTransportersBy(orderBy) {
    if (this.orderBy === orderBy) return;

    if (orderBy === 'less-price') {
      this.transporters.sort((a, b) => {
        return a.deliveryTypes[0].price - b.deliveryTypes[0].price;
      });
    } else if (orderBy === 'higher-price') {
      this.transporters.sort((a, b) => {
        return b.deliveryTypes[0].price - a.deliveryTypes[0].price;
      });
    }

    this.orderBy = orderBy;

    const compareTransporterList = document.querySelector(
      '.compare-transporter__list'
    );

    this.transporters.forEach((transporter, index) => {
      const prevTransporterElement = compareTransporterList.children[index];

      const nextTransporterElement = compareTransporterList.querySelector(
        `[id="${transporter.id}"]`
      );

      if (prevTransporterElement.id === nextTransporterElement.id) return;

      compareTransporterList.insertBefore(
        nextTransporterElement,
        prevTransporterElement
      );
    });
  }

  createCompareTableColumn(transporterID) {
    const transporter = this.transporters.filter(
      ({ id }) => id === transporterID
    )[0];

    const {
      id,
      nome,
      logoURL,
      armazenagem,
      tempoMercado,
      certificacao,
      deliveryTypes,
      equipeFeminina,
    } = transporter;

    const deliveryShared = deliveryTypes.filter(
      ({ type }) => type === 'Compartilhado'
    )[0];

    const deliveryExclusive = deliveryTypes.filter(
      ({ type }) => type === 'Exclusivo'
    )[0];

    return `
      <div class="table__column">
        <div
          class="transporter"
          data-transporter-id="${id}"
          data-transporter-name="${nome}"
          data-transporter-delivery-type-selected="Exclusivo"
        >
          <header class="transporter__header">
            <figure class="transporter__brand">
              <img
                class="transporter__logo"
                src="${logoURL}"
              />
            </figure>

            <h3 class="transporter__name">${nome}</h3>
          </header>

          <ul class="transporter__fields-list">
            <li class="list__item">
              ${
                deliveryShared
                  ? deliveryShared.priceFormatted
                  : 'Não disponivel'
              }
            </li>

            <li class="list__item">
              ${
                deliveryExclusive
                  ? deliveryExclusive.priceFormatted
                  : 'Não disponivel'
              }
            </li>

            <li class="list__item">
              ${certificacao ? 'Sim' : 'Não'}
            </li>

            <li class="list__item">
              ${equipeFeminina ? 'Sim' : 'Não'}
            </li>

            <li class="list__item">
              ${armazenagem ? 'Sim' : 'Não'}
            </li>

            <li class="list__item">
              <div class="transporter__select-custom">
                <select class="select__native">
                  ${deliveryTypes.map(({ type }) => {
                    return `<option value="${type}">${type}</option>`;
                  })}
                </select>

                <span class="select__custom-selected"></span>

                <div class="select__custom">
                  <ul class="select__list">
                  </ul>
                </div>

                <i class="fas fa-caret-down select__icon"></i>
              </div>
            </li>
          </ul>

          <button class="button button--primary js-button-schedule-change">Agendar</button>
        </div>
      </div>
    `;
  }

  createTransporterPriceElement(item) {
    let priceFormatted = item.priceFormatted;
    let StrongPrice = priceFormatted.substring(0, priceFormatted.length - 3).replace('R$ ', '');
    let decimals = priceFormatted.replace(`R$ ${StrongPrice}`, '');
    return `
      <div class="price" data-transporter-delivery-type="${item.type}">
        <div class="price__row">
          <div class="price__container price__container--flex">
            <label for="${item.id}">
              <div class="checkbox">
                <input
                  type="checkbox"
                  id="${item.id}"
                />
              </div>
            </label>
          </div>

          <div class="price__container price__container--right">
            <span class="price__total">R$ <span>${StrongPrice}</span>${decimals}</span>
          </div>
        </div>

        ${
          item.type === 'Compartilhado'
            ? `
          <p class="price__paragraph price__paragraph--text-orange">
            * Prazo de entrega: em até 45 dias da data da embalagem.
          </p>
        `
            : ''
        }
      </div>
    `;
  }

  createTransporterElement(transporterData) {
    const {
      id,
      nome,
      logoURL,
      features,
      descricao,
      deliveryTypes,
    } = transporterData;

    if (!deliveryTypes) {
      return '';
    }

    const showSeeMoreButton = descricao.length > 130;

    const prices = deliveryTypes
      .map(item => {
        return this.createTransporterPriceElement(item);
      })
      .join('');

    return `
      <li
        class="transporter"
        id="${id}"
        data-transporter-id="${id}"
        data-transporter-name="${transporterData.deliveryTypes[0].slaName}"
        data-transporter-delivery-type-selected="Exclusivo"
      >
        <div class="transporter__details" transporter-name="${nome}">
          <div class="transporter__row">
            <h3 class="transporter__name">${nome}</h3>
          </div>

          <div class="transporter__description">
            <p class="description__paragraph">
              ${descricao}
            </p>
            <p class="description__price">
              ${prices}
            </p>

            ${
              false
                ? '<button class="description__see-more">Ver tudo</button>'
                : ''
            }
          </div>

          <div class="transporter__row">
            <strong>Serviços inclusos:</strong>
            <ul class="transporter__benefits">
              ${features
                .split(';')
                .map(
                  feature => `
                    <li class="benefit">
                      <i class="far fa-check benefit__icon"></i>

                      <p class="benefit__text">
                        ${feature}
                      </p>
                    </li>
                  `
                )
                .join('')}
            </ul>
          </div>
        </div>

        <div class="transporter__prices">
          <hr/>
          <button class="button button--primary js-button-schedule-change">
            AGENDAR
          </button>
        </div>
      </li>
    `;
  }

  // Fetch Functions
  async fetchTransporters() {
    const fields =
      'armazenagem,certificacao,courierIds,descricao,equipeFeminina,features,frota,logo,nome,status,tempoMercado,id,tipoEmbalabem';

    try {
      if (this.hubID) {
        return await this.transporterService.getTransportersByHUB(
          this.hubID,
          fields
        );
      }

      throw new Error('The hubID is undefined');
    } catch (error) {
      window.bugsnagClient.notify(new Error(error));
    }
  }

  async fetchTransporterLogoURL(id, filename) {
    return await this.masterdataRepository.getAttachmentUrl(
      id,
      'logo',
      filename
    );
  }

  // Other Functions
  updateTransporterFooter() {
    const compareFooter = document.querySelector(
      '.compare-transporter__footer'
    );

    const compareFooterParagraph = compareFooter.querySelector(
      '.footer__paragraph'
    );

    const compareFooterButton = compareFooter.querySelector('.button');

    if (this.compareTransportersList.size > 0) {
      compareFooter.classList.add('compare-transporter__footer--show');
    } else {
      compareFooter.classList.remove('compare-transporter__footer--show');
    }

    if (this.compareTransportersList.size < 2) {
      compareFooterParagraph.innerHTML = `
        Selecione <span class="footer__paragraph--orange">2 ou mais empresas de mudança </span>para comparar
      `;

      compareFooterButton.classList.add('button--disabled');
    } else {
      compareFooterParagraph.innerHTML = `
        Você selecionou <span class="footer__paragraph--orange">${this.compareTransportersList.size} empresas de mudança</span>
      `;

      compareFooterButton.classList.remove('button--disabled');
    }
  }

  setTransporterSelected(transporterElement) {
    const {
      transporterId,
      transporterDeliveryTypeSelected,
    } = transporterElement.dataset;

    const index = this.transporters.findIndex(({ id }) => id === transporterId);

    this.transporterSelected = this.transporters[index];

    this.transporterSelected.deliveryTypeSelected = this.transporters[index].deliveryTypes[0];
  }

  updateSelectedSLA() {
    const { slaName } = 'Exclusivo';

    this.logisticsInfo = this.logisticsInfo.map(item => ({
      ...item,
      selectedSla: slaName,
    }));
  }

  async saveData() {
    try {
      this.updateSelectedSLA();

      await Promise.all([
        this.cartService.updateCheckoutTransporterSLA(this.logisticsInfo),
        this.cartService.setSingleCustomDataField(
          'selectedcourier',
          'courier_data',
          JSON.stringify(this.transporterSelected)
        ),
        this.cartService.setSingleCustomDataField(
          'orderinfo',
          'current_step',
          'transporter'
        ),
      ]);
    } catch (error) {
      console.log('saveData', error);
    }
  }

  async setLogoUrlInTransportes() {
    this.transporters = await Promise.all(
      this.transporters.map(async transporter => {
        const { id, logo } = transporter;

        const logoURL = await this.fetchTransporterLogoURL(id, logo);

        transporter.logoURL = logoURL;

        return transporter;
      })
    );
  }

  getTransporterVariations(logisticsInfo) {
    const transporterVariations = {};

    logisticsInfo.forEach(({ slas }) => {
      slas.forEach(({ id, price, name }) => {
        const key = id.toLowerCase().replace(/\s/g, '');

        if (transporterVariations[key]) {
          transporterVariations[key].price += price;
        } else {
          transporterVariations[key] = {
            price,
            name,
          };
        }
      });
    });

    return transporterVariations;
  }

  async setAddressCep() {
    const { customData } = await this.cartService.getCurrent();

    if (!customData) return;

    customData.customApps
      .filter(customApp => customApp.id === 'orderinfo')
      .forEach(customApp => {
        const { to_address_zipcode } = customApp.fields;

        this.addressCep = to_address_zipcode;
      });
  }

  setHubID() {
    const orderInfoApp = this.orderForm.customData?.customApps.find(
      customApp => customApp.id === 'orderinfo'
    );

    if (!orderInfoApp) return;

    const { hubID } = orderInfoApp.fields;

    this.hubID = hubID;
  }

  setTotalizers(totalizers) {
    this.totalizers = totalizers;
  }

  async getLogisticsInfo() {
    const {
      shippingData,
      totalizers,
    } = await this.cartService.simulateOrderForm(this.addressCep);

    this.setTotalizers(totalizers);

    this.logisticsInfo = shippingData.logisticsInfo;
  }

  async getDeliveryTypes() {
    await this.getLogisticsInfo();

    const insuranceSKU = this.orderForm.items.filter(
      ({ id }) => parseInt(id) === 35
    )[0];

    const transporterVariations = this.getTransporterVariations(
      this.logisticsInfo
    );

    const totalItemsWithDiscounts = this.totalizers
      .filter(total => total.id !== 'Shipping')
      .reduce((total, totalizer) => total + totalizer.value, 0);

    this.logisticsInfo[0].slas
      .map(courier => {
        const { courierId } = courier.deliveryIds[0];

        const index = this.transporters.findIndex(({ courierIds }) => {
          const ids = courierIds.split(';');

          return ids.includes(courierId);
        });

        courier.transporterIndex = index;

        return courier;
      })
      .filter(({ transporterIndex }) => transporterIndex !== -1)
      .forEach(({ id, name, deliveryIds, transporterIndex }) => {
        const key = id.toLowerCase().replace(/\s/g, '');
        const [courierName, courierType] = id.split(' - ');
        const courierTypeLowerCase = courierType.toLowerCase();

        const { courierId } = deliveryIds[0];

        let { price } = transporterVariations[key];

        price += insuranceSKU
          ? totalItemsWithDiscounts + -insuranceSKU.manualPrice
          : totalItemsWithDiscounts;

        const priceFormatted = this.helper.formatPrice(price);

        const currentTransporter = this.transporters[transporterIndex];

        const courier = [
          {
            price,
            priceFormatted,
            id: courierId,
            type: courierType,
            slaName: name,
          },
        ];

        if (currentTransporter.deliveryTypes) {
          currentTransporter.deliveryTypes = [
            ...courier,
            ...currentTransporter.deliveryTypes,
          ];
        } else {
          currentTransporter.deliveryTypes = courier;
        }

        currentTransporter[courierTypeLowerCase] = true;
      });

    this.removeTransportersWithoutDeliveryType();
    this.orderDeliveryTypesByLessPrice();
  }

  removeTransportersWithoutDeliveryType() {
    this.transporters = this.transporters.filter(transporter => {
      return transporter.hasOwnProperty('deliveryTypes');
    });
  }

  orderDeliveryTypesByLessPrice() {
    this.transporters = this.transporters.map(transporter => {
      transporter.deliveryTypes.sort((a, b) => a.price - b.price);

      return transporter;
    });
  }

  orderTransportersByLessPrice() {
    this.transporters.sort((a, b) => {
      return a.deliveryTypes[0].price - b.deliveryTypes[0].price;
    });
  }

  addTransportersToDOM() {
    const compareTransporterList = document.querySelector(
      '.compare-transporter__list'
    );

    const transporters = this.transporters.map(transporter =>
      this.createTransporterElement(transporter)
    );

    if (transporters.length > 0) {
      compareTransporterList.classList.remove('empty-list');
      compareTransporterList.innerHTML = transporters.join('');
    } else {
      compareTransporterList.classList.add('empty-list');
      compareTransporterList.innerHTML = `<li class="empty-text">Não encontramos nenhum resultado para os filtros selecionados.</li>`;
    }
  }
}

document.addEventListener('DOMContentLoaded', () => {
  new TransporterPage();
});
